@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

body {
  font-family: Madefor Display,Helvetica Neue,Helvetica,Arial,メイリオ,meiryo,ヒラギノ角ゴ pro w3,hiragino kaku gothic pro,sans-serif;
  background-color: rgb(054, 092, 189);
  line-height: 1.5;
  font-weight: bolde  r;
  
  }
header {
  grid-area: left;
  display: grid;
  text-align: center;
  font-size: 100px;
  margin-top : 50px;
  margin-bottom: 50px;
 
}

.container {
  display: flex;
  grid-template-columns: 800px 3fr;
  align-items: flex-start;
  
}

nav { /* 소개 프로젝트 등의 크기 글자크기*/
  grid-area: header;
  display: flex;
  font-family: Madefor Display,Helvetica Neue,Helvetica,Arial,メイリオ,meiryo,ヒラギノ角ゴ pro w3,hiragino kaku gothic pro,sans-serif;
  margin-right: 2000px;
  font-size: 100px;
}
main {
  display: flex;
  
  
  
  font-family: Madefor Display, Helvetica Neue, Helvetica, Arial, 메이리오, meiryo,
               ヒラギノ角ゴ pro w3, hiragino kaku gothic pro, sans-serif;
   /* Auto margin for horizontal centering */

  margin-right: auto; /* Auto margin for horizontal centering */
  background-color: rgb(183, 196, 230);
  
font-size: 30px;
width: 5000px;
height: 3100px;
  align-items: center;
  justify-content: left;
}

a:link, a:visited { /* 옆에 있는 소개 프로젝트 공모전 경력 등의 대한 정의*/
  background-color:rgb(054, 092, 189); 
  color:rgb(7, 7, 7);
  padding: 30px 150px;
  text-decoration: none;
  display: inline-block;
  width:800px;
  font-size: 1em;
}
a:hover, a:active { /* 링크를 마우스커서를 갖다대면 나오는 효과 정리*/
  background-color: rgb(12, 31, 82); 
  color:rgb(193, 193, 193) ;
}

button { /*이력서 자기소개서등에 대한 버튼 정리*/
  font-family: Madefor Display,Helvetica Neue,Helvetica,Arial,メイリオ,meiryo,ヒラギノ角ゴ pro w3,hiragino kaku gothic pro,sans-serif; 
  background-color: rgb(146, 168, 223);
  color: rgb(7, 7, 7) ;
  padding: 50px 100px;
  margin-left: 200px; 
  margin-bottom: 200px;
  font-size: 2em;
  cursor: pointer;
  transition-duration: 1s;
  position: static;
  top: 100px;
  left: 20px;
}
button:hover { /* 그 버튼들을 갖다댔을떄 나오는 효과 정리*/
  background-color: rgb(12, 31, 82);
  color: rgb(193, 193, 193);
}
.footer-content {
  display: flex;
  flex-direction: column; /* 세로로 나열하도록 지정 */
  align-items: center;
  text-align: center;
  padding: 20px; /* 원하는 패딩값으로 조절 */
}

.footer-content img {
  width: 800px; 
  margin: 100px 0;
  height: auto; 
  margin-right : 300px;
}

.footer-content p {
  margin: 100px 0; 
  margin-right : 400px;
  font-size : 5em;
  font-family: Madefor Display,Helvetica Neue,Helvetica,Arial,メイリオ,meiryo,ヒラギノ角ゴ pro w3,hiragino kaku gothic pro,sans-serif;
  
}
footer {
  display: flex;
  justify-content: flex-start; /* 내용을 왼쪽으로 정렬 */
  align-items: center;
  padding: 20px;
  margin-left: 300px;
}
.index img{
  width : 4700px;
  height: 3050px;
}